.my-portfolio {
  .tile {
    .container {
      position: relative;
      display: inline-block; //for overlay effect
      text-align: center;
      color: white;

      &:hover .overlay {
        opacity: 1;
      }

      .overlay {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: rgba(
          0,
          0,
          0,
          0.6
        ); /* Adjust the transparency and color as desired */
        opacity: 0;
        transition: opacity 0.3s ease;
      }
    }
  }
}
